import { Button } from '@mui/material';
import Papa from 'papaparse';
import React, { useContext, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { DataContext } from '..';
import DomainList from '../components/DomainList';

const Home = () => {
  const dataArray = useContext(DataContext);
  const [domains, setDomains] = useState([]);
  const navigate = useNavigate();

  const handleFileUpload = (e) => {
    const file = e.target.files[0];
    Papa.parse(file, {
      complete: (results) => {
        const newArray = results.data.slice(1, results.data.length - 1);
        setDomains(newArray);
        dataArray[0] = newArray;
        if (dataArray[1]) {
          dataArray.splice(1, 1);
        }
        return dataArray;
      },
    });
  };

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        height: '100vh',
      }}>
      {dataArray[1] && (
        <Button
          variant='contained'
          component='label'
          sx={{ margin: '20px' }}
          onClick={() => {
            navigate('/result');
          }}>
          Go to results
        </Button>
      )}
      <div
        style={{
          maxWidth: '500px',
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}>
        <Button variant='contained' component='label' sx={{ margin: '20px' }}>
          Upload Domain List
          <input onChange={handleFileUpload} hidden accept='.csv' type='file' />
        </Button>
        {!dataArray[1] && (
          <Button variant='contained' component='label' sx={{ margin: '20px' }}>
            Get URL Structure
            <input
              onClick={() => {
                if (domains.length > 0) {
                  // set uploaded domains to context
                  navigate('/result');
                } else {
                  alert('Please upload a domain list');
                }
              }}
              type='button'
              hidden
            />
          </Button>
        )}
      </div>
      <br />
      <DomainList domains={dataArray[0]} />
    </div>
  );
};

export default Home;
