import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import * as React from 'react';
import { useParams } from 'react-router-dom';
import { DataContext } from '..';

export default function DetailsTable() {
  const dataArray = React.useContext(DataContext);
  const { id } = useParams();
  const [tableData, setTableData] = React.useState([]);

  React.useEffect(() => {
    if (dataArray[1].length > 0) {
      const tableData = dataArray[1].filter((domainData) => {
        console.log(domainData, id);
        return domainData.id.toString() === id;
      });
      console.log(tableData);
      setTableData(tableData[0]?.urls);
    }
  }, [dataArray]);

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label='simple table'>
        <TableHead>
          <TableRow>
            <TableCell>URL</TableCell>
            <TableCell align='right'>STATUS</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {tableData?.map((row, index) => (
            <TableRow
              key={index}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
              <TableCell
                style={{
                  color: 'royalblue',
                  fontWeight: 'bold',
                }}
                component='th'
                scope='row'>
                {row.url}
              </TableCell>
              {row.status === 200 && (
                <TableCell
                  sx={{ color: 'green', fontWeight: 'bold' }}
                  align='right'>
                  {row.status}
                </TableCell>
              )}
              {row.status === 404 && (
                <TableCell
                  sx={{ color: 'red', fontWeight: 'bold' }}
                  align='right'>
                  {row.status}
                </TableCell>
              )}
              {row.status === 301 && (
                <TableCell
                  sx={{ color: 'orange', fontWeight: 'bold' }}
                  align='right'>
                  {row.status}
                </TableCell>
              )}
              {row.status !== 200 && row.status !== 301 && row.status !== 404 && (
                <TableCell
                  sx={{ color: 'royalblue', fontWeight: 'bold' }}
                  align='right'>
                  {row.status}
                </TableCell>
              )}
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
